<template>
<div class="app-page page-content-bottom-menu">
  <page-content class="page-content">
    <div class="div">
      <div @click="index = 1" :class="index === 1 ? 'divGreen1' : 'divBlack1'" class="div-1">农场介绍</div>
      <div @click="index = 2" :class="index === 2 ? 'divGreen1' : 'divBlack1'" class="div-2">农场活动</div>
      <div class="div-3"></div>
    </div>
    <van-row v-if="index === 1" v-html="data">
    </van-row>
    <van-row v-else>
      <span>近期活动</span>
      <van-row>
        <span>1近期活动</span>
      </van-row>
    </van-row>
  </page-content>
</div>

</template>
<script>
import PageContent from '@/components/page/content'
export default {
  name: 'popularize',
  components: {
    PageContent
  },
  data () {
    return {
      index: 1,
      button1: 'row-2-button2',
      button2: 'row-2-button',
      data: '<p><img src="http://120.24.54.126:9010/app/api/file/download?fileName=324798cd211143a59db7a9ced31e9a09.png">2222</p><p><img src="http://120.24.54.126:9010/app/api/file/download?fileName=324798cd211143a59db7a9ced31e9a09.png">2222</p><p><img src="http://120.24.54.126:9010/app/api/file/download?fileName=324798cd211143a59db7a9ced31e9a09.png">2222</p><p><img src="http://120.24.54.126:9010/app/api/file/download?fileName=324798cd211143a59db7a9ced31e9a09.png">2222</p><p><img src="http://120.24.54.126:9010/app/api/file/download?fileName=324798cd211143a59db7a9ced31e9a09.png">2222</p>'
    }
  },
  methods: {},
  created () {
  },
  watch: {
    index (value) {
      if (value === 1) {
        document.getElementsByClassName('div-3')[0].style.left = '0%'
      } else {
        document.getElementsByClassName('div-3')[0].style.left = '50%'
      }
    }
  }
}

</script>

<style rel='stylesheet/scss' lang='scss' scoped>
  @import '@/styles/variables.scss';
  .div{
    .div-3{
      position: relative;
      left: 0px;
      top: 84px;
      height: 4px;
      width: 50%;
      background-color: $green22ac38;
    }
    .divBlack1{
      color: $black333333;
    }
    .divGreen1{
      color: $green22ac38;
    }
    .div-2{
      font-family: $font-family-Medium;
      font-size: 28px;
      font-weight: normal;
      font-stretch: normal;
      text-align: center;
      line-height: 88px;
      background-color: $whiteffffff;
      float: left;
      height: 88px;
      width: 50%;
    }
    .div-1{
      font-family: $font-family-Medium;
      font-size: 28px;
      font-weight: normal;
      font-stretch: normal;
      text-align: center;
      line-height: 88px;
      background-color: $whiteffffff;
      float: left;
      height: 88px;
      width: 50%;
    }
    height: 88px;
    width: 750px;
  }
  .row-1{
    background-color: #cacaca;
    font-size: 30px;
    height: 100px;
    width: 100%;
  }
  .row-2-button{
    margin: 15px 30px;
  }
  .row-2-button2{
    margin: 15px 30px;
    background-color: #cacaca;
    color: white;
  }
</style>



